html {
  background: url(/pastforward/static/media/background.b6243e71.jpg);
  background-size: cover;
  font-size: 16px;
  color: rgb(151, 141, 122);
}

@media(min-width:60em) {
  html {
    font-size: 100%
  }
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

p {
  font-size: 250%;
  line-height: 1.2;
}

a {
  color: rgb(151, 141, 122);
  text-decoration: none;
}

h1 {
  font-size: 500%;
  display: inline-block;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 350%;
  }
}

@font-face {
  font-family: 'GermaniaVersalien';
  src: url(/pastforward/static/media/GermaniaVersalien.d36b44b6.ttf) format('truetype');
}

@font-face {
  font-family: 'DistortionDosAnalogue';
  src: url(/pastforward/static/media/DistortionDosAnalogue.8faf7581.otf) format('truetype');
}

.App-header {
  margin: 0 0 0 5vh;
  color: white;
}

.App-past {
  font-family: GermaniaVersalien, arial, sans-serif;
  margin: 0 1vh 0 0;
}

.App-forward {
  font-family: DistortionDosAnalogue, arial, sans-serif;
  margin: 0;
}

.App-forward:after {
  content: '\23E9';
  height: 28px;
  width: 28px;
  opacity: 0.75;
}

.App-About {
  float: right;
  font-size: 2em;
  margin: 2vh 2vh 0 0;
}

.App-About:before {
  content: "\24D8";
}

.ImageList {
  position: relative;
  display: block;
  width: 100%;
  height: 100vh;
  float: left;
}
.Image {
  display: block;
  position: absolute;
  z-index: 1;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  top: 0px;
  left: 0px;
  width: 50%;
  height: auto;
  border: 0.75rem transparent solid;
  background-color: #eee;
  margin: 0;
  padding: 0;
  touch-action: none;
}
@font-face {
  font-family: 'Berolina';
  src: url(/pastforward/static/media/Berolina.d61b8938.ttf) format('truetype');
}

video {
  position: relative;
  width: 100%;
  height: 90vh;
  z-index: 9999;
}

.intro,
.association,
.end,
.about {
  position: relative;
  z-index: 9999;
  background: url(/pastforward/static/media/intro.df666b0a.png);
  width: 100%;
  height: 55rem;
  background-size: 100% 100%;
}

.intro-Text,
.end-Text{
  display: block;
  font-family: Berolina, arial, sans-serif;
  padding: 15vh 5vh;
  text-align: center;
}

.about-Text {
  display: block;
  font-family: Berolina, arial, sans-serif;
  text-align: center;
  font-size: 26px;
  padding: 175px 100px 0;
  margin: 0;
}

@media only screen and (max-width: 768px) {
  .about-Text {
    font-size: 18px;
    padding: 150px 50px 0;
  }
}

@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .about-Text {
    font-size: 24px;
    padding: 160px 60px 0;
  }
}

.association-Text {
  display: block;
  font-family: Berolina, arial, sans-serif;
  padding: 0vh 10vh;
  text-align: center;
}

.association-Container {
  float: left;
  width: 50%;
}

.association-Image {
  width: 60%;
  padding: 15vh 12vh 0;
}

